import React from "react";
export const OrderColumns  =[
    {
        title: " ",
        dataIndex: 'paymentType'
    },
    {
        title: " ",
        dataIndex: 'desiredDeliveryDate',
    },
    {
        title: "",
        dataIndex: 'orderStatus',
        render: (value)=>
            value===' '  || value ===',  ' ?
            <span style={{background: '#FF6800',color: '#f2f2f2',borderRadius: '5px',padding: '3px',margin: '3px 0'}}>{value}</span>
            :value==='' || value === '  ' ? <span style={{background: '#009B00',color: '#f2f2f2',borderRadius: '5px',padding: '3px'}}>{value}</span>
            :value==='' ? <span style={{background: '#fff',color: '#000',borderRadius: '5px',padding: '3px'}}>{value}</span> : value

        }

]