

export const createOrChangeTemplate = async (
    dispatch,
    func,
    data,
    toggleLoader,
    id='',
    showNotification
    )=>{

    dispatch(toggleLoader);
    await func(data,id).then(response => {
        const notifObj = {
            isOpen: true,
            title: response.data?.resultCode === 'DUPLICATE' ? '!' : '!',
            body: response.data?.resultCode === 'DUPLICATE' ? '     !' : id ? ' !':' !'
        }
        dispatch(showNotification(notifObj))
    }).catch(()=>dispatch(showNotification({
        isOpen: true,
        title: '!',
        body:  id ? '  !' :'  !'
    })))
    dispatch(toggleLoader)
}