export const toBeautifyFieldsValue = (value)=>{
    switch (value) {

        case 'CASH':
            return ''
        case 'ELSOM':
            return ''
        case 'COURIER':
            return ''
        case 'STORAGE':
            return ''
        case 'AWAITING_DELIVERY':
            return ' '
        case 'PAID_AWAITNG_DELIVERY':
            return ',  '
        case 'DELIVERED_AND_PAID':
            return '  '
        case 'DELIVERED':
            return ''
        case 'CLOSED':
            return ''
        case null:
            return ' '
        case 'null':
            return ' '
        case '':
            return ' '
        case NaN:
            return ' '
        case undefined:
            return ' '
        case 'SOM':
            return ''
        case 'USD':
            return '$'
        default:
            return value
    }
}