export const toUglyfyFieldsValue = (value)=>{
    switch (value) {

        case '':
            return 'CASH'
        case '':
            return 'ELSOM'
        case '':
            return 'COURIER'
        case '':
            return 'STORAGE'
        case ' ':
            return 'AWAITING_DELIVERY'
        case '  ':
            return 'DELIVERED_AND_PAID'
        case '':
            return 'CLOSED'

        default:
            return value
    }
}